import React from "react";
import InfoBookstitle from "./InfoBooktitle";
import './InfoBooks.css';
import ButtonLayout from "../../UI/Button/ButtonLayout/ButtonLayout";
import CartImage from '../../../assets/Images/icon/vuesax-add-to-cart-black.svg';



const InfoBooks = () => {
   return (
      <div className="infobooks-parent">
         <InfoBookstitle customstyle="head-title-info-book">    </InfoBookstitle>
         <InfoBookstitle customstyle="sub-head-info-book">    </InfoBookstitle>
         <div className="publisher-info">
            <InfoBookstitle customstyle="publisher-name"> </InfoBookstitle>
            <div className="date-publish">
               <InfoBookstitle> : </InfoBookstitle>
               <InfoBookstitle>1399</InfoBookstitle>
            </div>
         </div>
         <ButtonLayout button='btn-download-info-book' spancolordark="color-dard" src={CartImage} alt={'cartimg'} title={'   '} />
      </div>
   )
}

export default InfoBooks;